.subtotal{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 300px;
    height: 150px;
    padding: 20px;
    background-color: #f3f3f3;
    border: 1px solid #dddddd;
    border-radius: 3px;
    position: fixed;
    right: 2vw;
}

.subtotal__gift{
    display: flex;
    align-items: center;
    margin: 10px;
}

.subtotal__gift > input{
    margin-right: 5px;
}

.subtotal > button{
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    /* height: 30px !important; */
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
    outline-style: none;
    outline: none;
}

.subtotal > button:active{
    border: 1px solid black;
}

