.product{
    display: flex !important;
    flex-direction: column !important;

    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;

    background-color: white;
    z-index: 1;
}
.product > img{
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}

.product__button{
    background: #f0c14b;
    border: 1ppx solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
    outline: none;
    outline-style: none;
}

.product__price{
    margin-top: 5px;
}

.product__info{
    width: 90%;
    height: 100px;
    margin-bottom: 15px;
}
